.home {
  width: 100%;
  display: flex;
  align-items: center;
  position: relative;
  justify-content: center;
  margin: auto;
}
.home .overlay {
  position: absolute;
  height: 100%;
  width: 100%;
  background: rgb(47, 106, 127);
  top: 0;
  bottom: 0;
  z-index: 1;
  mix-blend-mode: hard-light;
}
.home video {
  position: absolute;
  height: 100%;
  width: 100%;
  top: 0;
  bottom: 0;
  -o-object-fit: cover;
     object-fit: cover;
}
.home .homeContent {
  width: 100%;
  height: -moz-max-content;
  height: max-content;
  padding: 8rem 1.5rem 3rem;
  display: flex;
  row-gap: 3rem;
  flex-direction: column;
  align-items: stretch;
  justify-content: center;
  margin: auto;
  color: var(--white-color);
  z-index: 100;
}
.home .homeContent .textDiv {
  padding: 2rem 0;
}
.home .homeContent .textDiv .smallText {
  font-size: 13px;
  text-transform: uppercase;
}
.home .homeContent .textDiv .homeTitle {
  position: relative;
  font-size: 1.8rem;
}
.home .cardDiv {
  position: relative;
  padding: 2rem 1rem;
  background: var(--white-color);
  border-radius: 1rem;
  gap: 1rem;
  color: var(--text-color);
}
.home .cardDiv label {
  display: inline-block;
  padding-bottom: 0.2rem;
}
.home .cardDiv .label_total {
  align-items: center;
  text-align: center;
  justify-content: space-between;
  color: var(--text-color);
}
.home .cardDiv .label_total .total {
  color: var(--text-color);
  font-size: 18px;
}
.home .input {
  height: 40px;
  padding: 0 1rem;
  background: var(--input-color);
  border-radius: 2rem;
}
.home .input input {
  background: none;
  border: none;
  outline: none;
  width: 100%;
  font-size: 13px;
  font-weight: 500;
}
.home .input input[type=range], .home .input input[type=date], .home .input input[type=date]::-webkit-calendar-picker-indicator {
  cursor: pointer;
}
.home .input input::-moz-placeholder {
  font-size: 13px;
}
.home .input input::placeholder {
  font-size: 13px;
}
.home .input .icon {
  font-size: 22px;
  color: var(--primary-color);
  margin-left: 10px;
}
.home .searchOptions {
  position: absolute;
  background: var(--gradient-color);
  width: -moz-max-content;
  width: max-content;
  bottom: -0.7rem;
  left: 50%;
  -moz-column-gap: 0.8rem;
       column-gap: 0.8rem;
  padding: 0.5rem 1rem;
  border-radius: 3rem;
  transform: translate(-50%);
  justify-content: center;
  cursor: pointer;
}
.home .searchOptions:hover {
  background: var(--primary-color);
}
.home .searchOptions .icon {
  font-size: 22px;
}
.home .searchOptions p {
  font-size: 13px;
}
.home .homeFooterIcons {
  width: 100%;
  justify-content: space-between;
}
.home .homeFooterIcons .icon {
  font-size: 24px;
  margin: 0 0.5rem;
  color: var(--white-color);
  font-weight: 600;
}
.home .homeFooterIcons .icon:hover {
  color: var(--primary-color);
}

@media screen and (min-width: 500px) {
  .home .homeContent .textDiv .smallText {
    font-size: 16px;
  }
  .home .homeContent .textDiv .homeTitle {
    position: relative;
    font-size: 2.3rem;
    width: -moz-max-content;
    width: max-content;
  }
  .home .homeContent .textDiv .homeTitle::after {
    position: absolute;
    content: "";
    background: var(--secondary-color);
    height: 5px;
    width: 145px;
    right: 0px;
    bottom: 5px;
    z-index: -1;
  }
  .home .homeContent .cardDiv {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media screen and (min-width: 710px) {
  .home .homeContent .cardDiv {
    padding: 2rem 1rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
@media screen and (min-width: 940px) {
  .home {
    height: 85vh;
  }
}
@media screen and (min-width: 1240px) {
  .home {
    height: 95vh;
  }
  .home .cardDiv {
    padding: 2rem 1rem;
    grid-template-columns: repeat(3, 1fr);
  }
  .home .cardDiv .input {
    margin-bottom: 1rem;
  }
  .home .cardDiv .searchOptions {
    bottom: -1rem;
    padding: 8px 1rem;
  }
}/*# sourceMappingURL=home.css.map */